.forselectingvalues{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.forgeneratebutton{
    border-radius: 6px;
    background-color: #00539CFF;
    color: #EEA47FFF;
    cursor: pointer !important; 
    padding: 14px;
    margin: 33px;   
    &:hover{
        background-color:#EEA47FFF ;
        color:#00539CFF;
    }
}
.forelementwithlabel{
    display: flex;
    flex-direction: column;
    margin: 14px;
    &>input{
        padding: 13px;
    }
    &>label{
        margin-bottom: 3px;
    }
}
.forHeading{
    display: flex;
    background-color: #F96167;
    color: white;
    justify-content: center;
    padding: 31px;
    font-size: 38px;    
}

.forelementwithvalue{
    display: flex;
    flex-direction: column;
    margin: 14px;
    &>div{
        padding: 13px;
        background-color: white;

    }
    &>label{
        margin-bottom: 3px;
    }
}
.forweatherdiv{
    display: flex;
    //justify-content: center;
    margin: 0% 14.8%;
    flex-wrap: wrap;
}
